/* General Styles */
.header {
  background-color: #E3530F; /* New color for the header */
  padding: 10px;
  text-align: left;
  display: flex;
  align-items: center;
  font-family: 'Arial', sans-serif; /* Change to desired font */
  color: white;
  font-weight: 600;
}

.logo {
  height: 30px; /* Adjust the height as needed */
  margin-left: 20px; /* Add left margin for spacing */
}

.video-container {
  text-align: center; /* Center-align the video */
}

.video {
  width: 100%;
  height: 47vh; /* Set the height to 47% of the viewport */
  max-width: 100%; /* Responsive video width */
}

.payment-solutions {
  font-family: Arial, sans-serif;
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

h1, h2 {
  color: #8b0000;
  text-align: center;
}

.paragraf {
  font-size: 19px;
  font-weight: bold;
}

.solution-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-bottom: 20px;
}

.solution-card {
  background-color: #fff;
  padding: 15px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.solution-card img {
  width: 60px;
  height: 60px;
  margin-bottom: 10px;
  border-radius: 8px; /* Rounded corners */
  padding: 5px; /* Padding inside the border */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); /* Shadow for better visibility */
  background-color: #E3530F;
}

.solution-card h3 {
  margin: 0;
  font-size: 16px;
  color: #8b0000; /* Change this color to the desired one */
}

.solution-card p {
  font-size: 12px;
  color: #666;
}

.toggle {
  width: 40px;
  height: 20px;
  background-color: #ddd;
  border-radius: 10px;
  margin: 10px auto 0;
  position: relative;
}

.toggle-slider {
  width: 48px;
  height: 18px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 1px;
  left: 1px;
  transition: all 0.3s ease;
}

.toggle-slider.active {
  left: 21px;
  background-color: #f2f0f5;
}

.get-growing-btn {
  display: block;
  width: 300px;
  height: 50px;
  margin: 20px auto;
  background-color: #8b0000;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 30px;
  cursor: pointer;
  font-weight: 900;
}

.info-section {
  flex: 2;
  height: 250px;
  margin-left: 110px;
}

.info-section h2 {
  font-size: 1em;
  margin-bottom: 20px;
}

.info-section ul {
  list-style-type: none;
  padding: 0;
}

.info-section li {
  margin-bottom: 15px;
  position: relative;
  padding-left: 25px;
}

.info-section li::before {
  content: 'â€¢';
  position: absolute;
  left: 0;
  color: white;
}

.image-section {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.circular-image {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.966);
  position: absolute;
  left: 25px; /* Adjusted positioning */
}

.circular-image img {
  width: 80%;
  height: 80%;
}

/* Responsive Styles */

/* Medium Devices (Tablets) */
@media (max-width: 768px) {
  .header {
      flex-direction: column;
      align-items: flex-start;
  }

  .logo {
      margin-left: 0;
  }

  .solution-grid {
      grid-template-columns: repeat(2, 1fr); /* Adjust grid layout for tablets */
  }

  .solution-card img {
      width: 50px;
      height: 50px;
  }

  .get-growing-btn {
      width: 100%; /* Full width button on smaller screens */
  }

  .info-section {
      margin-left: 20px;
      text-align: center;
  }

  .circular-image {
      left: 0;
      width: 250px;
      height: 250px;
  }
}

/* Small Devices (Phones) */
@media (max-width: 480px) {
  .solution-grid {
      grid-template-columns: 1fr; /* Stack cards vertically on small screens */
  }

  .solution-card img {
      width: 40px;
      height: 40px;
  }

  .solution-details h2 {
      font-size: 14px;
  }

  .solution-details ul {
      padding-left: 5px; /* Further adjust padding */
  }

  .get-growing-btn {
      font-size: 20px;
      height: 40px;
  }

  .circular-image {
      width: 200px;
      height: 200px;
  }
}

/* Footer Styling */
.icici-footer1 {
  display: flex; 
  justify-content: space-between; 
  align-items: center; 
  background-color: #8B0000;
  color: white;
  padding: 10px;
  font-size: x-small;
}

.icici-footer1 p {
  margin: 0; 
}

.terms, .address {
  margin: 10px 0;
}

.click-here {
  color: #fff; /* Link color */
  text-decoration: underline; /* Underline for link */
}

.click-here:hover {
  color: #d3d3d3; /* Hover color */
}

.scroll-wrapper {
  display: flex;
  overflow-x: hidden;
  scroll-behavior: smooth;
  overflow-y: hidden;
}

.scroll-container {
  min-width: 90%;
  flex: none;
  display: flex;
  background-color: #FF6600;
  border-radius: 20px;
  padding: 10px;
  color: white;
  position: relative;
  margin-left: 50px;
}

.dots-container {
  text-align: center;
  justify-content: center;
  margin-left: 90%;
  width: 50px;
  height: 20px;
  background: red;
  border-radius: 50%;
  margin-top: -20px;
}

.dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 2px;
  border-radius: 50%;
  background-color: #ccc;
  cursor: pointer;
}

.dot.active {
  background-color: #333;
}

.solution-icons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  max-width: 800px;
  margin: 0 auto;
}

.solution-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}

.icon-circle {
  width: 120px;
  height: 120px;
  background-color: #FF6600;
  border-radius: 50%;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.icon-circle img {
  width: 80%;
  height: 80%;
  margin-top: -70px;
}

.solution-icon span {
  font-size: 12px;
  font-weight: bold;
  text-align: center;
  color: white;
}

/* Small Devices (Phones) */
@media (max-width: 600px) {
  .solution-icons {
      flex-direction: column;
  }

  .icon-circle {
      width: 100px;
      height: 100px;
  }
}
